import { Hono } from 'hono';
import { eq, desc } from 'drizzle-orm';
import { wallArtProducts } from '../db/schema';
import { adminAuth } from '../middleware/auth';
import type { Bindings, Variables } from '../index';

export const wallArtRoutes = new Hono<{ Bindings: Bindings; Variables: Variables }>();

// GET /api/wall-art - List all wall art products (public)
wallArtRoutes.get('/', async (c) => {
  const db = c.get('db');
  const category = c.req.query('category');

  let query = db.select().from(wallArtProducts).where(eq(wallArtProducts.archived, false));

  const result = await query.orderBy(wallArtProducts.displayOrder, desc(wallArtProducts.createdAt)).all();

  const filtered = category ? result.filter(p => p.category === category) : result;

  return c.json(filtered);
});

// GET /api/wall-art/:id - Get single wall art product (public)
wallArtRoutes.get('/:id', async (c) => {
  const db = c.get('db');
  const id = c.req.param('id');

  const product = await db.select().from(wallArtProducts).where(eq(wallArtProducts.id, id)).get();

  if (!product) {
    return c.json({ error: 'Wall art product not found' }, 404);
  }

  return c.json(product);
});

// POST /api/wall-art - Create wall art product (admin only)
wallArtRoutes.post('/', adminAuth, async (c) => {
  const db = c.get('db');
  const body = await c.req.json();

  const slug = body.name.toLowerCase().replace(/[^a-z0-9]+/g, '-').replace(/(^-|-$)/g, '');

  const result = await db.insert(wallArtProducts).values({
    name: body.name,
    slug: slug,
    price: body.price,
    currency: body.currency || 'AUD',
    category: body.category,
    shortDescription: body.shortDescription,
    longDescription: body.longDescription,
    dimensions: body.dimensions,
    image: body.image,
    detailImages: body.detailImages || [],
    badge: body.badge,
    availability: body.availability || 'In stock',
    displayOrder: body.displayOrder || 0,
  }).returning().get();

  return c.json(result, 201);
});

// PUT /api/wall-art/:id - Update wall art product (admin only)
wallArtRoutes.put('/:id', adminAuth, async (c) => {
  const db = c.get('db');
  const id = c.req.param('id');
  const body = await c.req.json();

  const result = await db.update(wallArtProducts)
    .set({
      ...body,
      updatedAt: new Date().toISOString(),
    })
    .where(eq(wallArtProducts.id, id))
    .returning()
    .get();

  if (!result) {
    return c.json({ error: 'Wall art product not found' }, 404);
  }

  return c.json(result);
});

// DELETE /api/wall-art/:id - Archive wall art product (admin only)
wallArtRoutes.delete('/:id', adminAuth, async (c) => {
  const db = c.get('db');
  const id = c.req.param('id');

  await db.update(wallArtProducts)
    .set({ archived: true, updatedAt: new Date().toISOString() })
    .where(eq(wallArtProducts.id, id));

  return c.json({ success: true });
});
